import { Component, OnInit } from '@angular/core';
import { SubCategoryService } from '../../../../../auth/_services/subCategory.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
@Component({
    selector: 'app-sub-category-list',
    templateUrl: "sub-category-list.component.html",
    styles: []
})
export class SubCategoryListComponent implements OnInit {

    form: FormGroup;
    private formSumitAttempt: boolean;

    constructor(private toastr: ToastrService, private _subCategoryService: SubCategoryService, private formBuilder: FormBuilder) { }

    public subCategoryData = "";
    public searchValue = "";
    public dropDownValue = '';
    public noOfPage = '';
    public menuDrpdown: any;
    public categoryDrpdown = '';
    public selectedMenu = '';
    public selectedStatus = '';
    public selectedCategory = '';
    p: number = 1;

    ngOnInit() {
        localStorage.setItem('section', 'subCategory');
        this._subCategoryService.getAll().subscribe(
            data => {
                this.subCategoryData = data.success;
                this.menuDrpdown = data.menu;
                console.log(this.subCategoryData);
                //console.log(this.menuDrpdown);
            }
        );


        this.form = this.formBuilder.group({
            menu_id: [null, Validators.required],
            category_id: [null, Validators.required],
            name: [null, Validators.required],
            status: [null, Validators.required]
        });
    }


    isFieldValid(field: string) {
        return (
            (!this.form.get(field).valid && this.form.get(field).touched) ||
            (this.form.get(field).untouched && this.formSumitAttempt)
        );
    }

    displayFieldCss(field: string) {
        return {
            'has-error': this.isFieldValid(field),
            'has-feedback': this.isFieldValid(field)
        };
    }

    onSubmit() {
        this.formSumitAttempt = true;
        if (this.form.valid) {
            let that = this;
            this._subCategoryService.getByName(this.form.value.name)
                .subscribe(
                searchData => {
                    if (searchData.success) {
                        this.toastr.error('Sub-category Already Exist', 'Error Message');
                    } else {
                        this._subCategoryService.createSubCategory({ form_data: this.form.value }).subscribe(
                            data => {
                                this.subCategoryData = data.success;
                                this.menuDrpdown = data.menu;
                                this.toastr.success('Sub-category Added Successfully', 'Success Message');
                                this.reset();
                            }
                        );
                    }

                }
                );

        }
    }

    reset() {
        if (this.selectedMenu != '' && this.selectedCategory != '' && this.selectedStatus != '') {
            this.form.reset();
            this.selectedMenu = '';
            this.selectedStatus = '';
            this.selectedCategory = '';
            this.formSumitAttempt = false;
        }
    }



    public subCategory_delete(del_id) {
        console.log(del_id);
        let value = confirm('Are u want to delete this sub-category?');
        if (value) {
            this._subCategoryService.subCategoryDelete(del_id).subscribe(
                data => {
                    this.subCategoryData = data.success;
                    this.menuDrpdown = data.data;

                }

            );
        }
    }


    public changeSubCategoryStatus(subCategory: any) {

        console.log(subCategory);

        if (subCategory.status == 'Active') {
            subCategory.status = 'Inactive';
            this._subCategoryService.updateStatus(subCategory.id).subscribe(

                data => {
                    console.log('update', data);
                }
            );
        }
        else {
            subCategory.status = 'Active';
            this._subCategoryService.updateStatus(subCategory.id).subscribe(

                data => {
                    console.log('update', data);
                }
            );
        }
    }

    public search() {
        this._subCategoryService.searching({ search_value: this.searchValue, dropdown_value: this.dropDownValue }).subscribe(
            data => {
                this.subCategoryData = data.success;
            }
        );
    }



    public get_category() {
        console.log(this.selectedMenu);
        this._subCategoryService.getCategory({ menu_id: this.selectedMenu }).subscribe(
            data => {
                this.categoryDrpdown = data.success;
                console.log(this.categoryDrpdown);
            }

        )

    }






}
